*{
  margin: 0;
  padding: 0;
  font-family: Arial, Helvetica, sans-serif;
}
a{
  text-decoration: none;
}
.navbar{
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding: 1rem 3rem;
  align-items: center;
  justify-content: space-between;
}
.nav-left{
  width: 100%;
}
.nav-left>strong{
  font-size: 1.4rem;
}
.nav-left>strong>a{
  color: #4a4a4a;
}
.input-section{
  width: 100%;
  display: grid;
  grid-template-columns: 3rem 1fr;
  background-color: #dfdfdf;
  align-items: center;
  padding: .3rem;
  border-radius: .4rem;
}
.input-section>label{
  display: flex;
  align-items: center;
  justify-content: center;
  color: #9b9b9b;
  font-size: 1.2rem;
}
.input-section input{
  border: none;
  font-size: 1.1rem;
  background-color: #dfdfdf;
  color: #9b9b9b;
  outline: none;
}
.nav-right .fa-home{
  color: #4a4a4a;
  font-size: 1.3rem;
  float: right;
}
nav{
  border-bottom: 3px solid #dfdfdf;
}
.home{
  padding: 1rem 3rem;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-gap: 1rem;
}
.card{
  box-shadow: 2px 2px 8px gray;
  border-radius: .5rem;
  width: 100%;
}
.card-img>img{
  width: 100%;
  height: 100%;
  border-top-left-radius: .5rem;
  border-top-right-radius: .5rem;
}
.card-text{
  padding: 3px;
  padding-bottom: 2rem;
}
.card-text>div{
  display: grid;
  grid-template-columns: 1fr 3.2rem;
}
.card-text>div>h1{
  color: #4a4a4a;
  font-size: .9rem;
}
.card-text>div>p{
  color: #9b9b9b;
}
.card-text>p{
  padding-top: .4rem;
  color: #9b9b9b;
  padding-bottom: 1rem;
  font-size: .9rem;
  letter-spacing: 1px;
  line-height: 1.2rem;
  height: 1.3rem;
  width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}
.details{
  padding: 1rem 3rem;
  display: grid;
  grid-template-columns: 15rem 4fr;
}
.img-details img{
  width: 100%;
}
.detail-card{
  padding-left: 1rem;
}
.detail-card>div{
  display: flex;
  align-items: center;
  font-size: .9rem;
  padding-bottom: .2rem;
}
.detail-card>div>h1{
  color: #4a4a4a;
}
.detail-card>p{
  font-size: 1.4rem;
  padding: .2rem 0;
  letter-spacing: 1px;
  line-height: 1.8rem;
  color: #4a4a4a;
}
.rating span{
  color: orange;
}
.loading{
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 5rem 1rem;
  font-size: 2.5rem;
}
@media only screen and (max-width: 1000px){
  .home{
    grid-template-columns: repeat(4, 1fr);
  }
}
@media only screen and (max-width: 800px){
  .home{
    grid-template-columns: repeat(3, 1fr);
  }
  .details{
    grid-template-columns: 1fr;
  }
}
@media only screen and (max-width: 600px){
  .home{
    grid-template-columns: repeat(2, 1fr);
  }
  .detail-card>div{
    display: grid;
    grid-template-columns: 1fr;
  }
}